import React, { useEffect } from 'react'
import { Dialog, DialogContent, IconButton } from '@mui/material'
import { IconClose } from '../../../../assets/Icons'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import VacancyAbout from '../VacancyAbout/VacancyAbout'
import { closeVacancyPopup } from '../../../../services/slices/student-vacancies-slice'
import { getExternalVacancy } from '../../../../services/axios/student-vacancies'

const AboutNonPartnersVacancyPopup = () => {
  const dispatch = useAppDispatch()
  const {
    isOpenVacancyPopup,
    selectedVacancy: littleVacancy,
    selectedFullVacancy: selectedVacancy,
  } = useAppSelector((state) => state.studentVacancies)

  useEffect(() => {
    if (littleVacancy && isOpenVacancyPopup) dispatch(getExternalVacancy(littleVacancy.id))
  }, [isOpenVacancyPopup, littleVacancy])

  const handleClose = () => {
    dispatch(closeVacancyPopup())
  }

  return (
    <Dialog
      open={isOpenVacancyPopup}
      onClose={handleClose}
      sx={{
        '& .MuiDialog-paper': {
          width: 710,
          minWidth: 710,
          maxWidth: 710,
          padding: 0,
        },
        '& .MuiDialog-container': {
          alignItems: 'flex-start',
        },
      }}
    >
      <IconButton
        onClick={handleClose}
        sx={{
          position: 'absolute',
          right: 18,
          top: 17,
          zIndex: 4,
        }}
      >
        <IconClose />
      </IconButton>
      <DialogContent sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
        {selectedVacancy && <VacancyAbout isPartner={false} />}
      </DialogContent>
    </Dialog>
  )
}

export default React.memo(AboutNonPartnersVacancyPopup)
