/* eslint-disable no-unused-vars */
import React from 'react'
import { LinearProgress, Snackbar, SnackbarContent, Slide } from '@material-ui/core'
import useStyles from './CustomSnackBar.style'
import IconOk from '../../../../assets/Icons/IconOk/IconOk'

function TransitionLeft(props) {
  return <Slide {...props} direction="right" />
}

const CustomSnackBar = ({ open, onClose, header, message }) => {
  const classes = useStyles()
  const [progress, setProgress] = React.useState(100)

  React.useEffect(() => {
    if (open) {
      setProgress(0)
    } else {
      setProgress(100)
    }
  }, [open])

  return (
    <Snackbar
      open={open}
      onClose={onClose}
      autoHideDuration={6000}
      TransitionComponent={TransitionLeft}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
    >
      <SnackbarContent
        className={classes.root}
        message={
          <>
            <div className={classes.content}>
              <IconOk className={classes.icon} />
              <div className={classes.messageContainer}>
                <span className={classes.header}>{header}</span>
                <span className={classes.message}>{message}</span>
              </div>
            </div>
            <LinearProgress
              variant="determinate"
              classes={{
                bar: classes.bar,
                root: classes.barRoot,
              }}
              value={progress}
            />
          </>
        }
      />
    </Snackbar>
  )
}

export default CustomSnackBar
