/* eslint-disable no-unused-vars */
import React, { useEffect } from 'react'
import { Controller, useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { IconButton, FormHelperText } from '@material-ui/core'
import { useNavigate } from 'react-router-dom'
import { Typography } from '@mui/material'
import Button from '@material-ui/core/Button'
import Dialog from '@mui/material/Dialog'
import DialogTitle from '@mui/material/DialogTitle'
import DialogContent from '@mui/material/DialogContent'
import DialogActions from '@mui/material/DialogActions'
import useStyles from './TermsOfUse.styles'
import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import useAsync from '../../../../hooks/useAsync'
import CheckboxItem from '../../../Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import api from '../../../../api/api'
import { API_GET_TERM_OF_USE, API_POST_TERM_OF_USE } from '../../../../utils/constants'
import { termsOfUseSchema } from '../../../../utils/schemas'
import processServerError from '../../../../utils/server-error'
import { useAppDispatch } from '../../../../services/store/hooks'
import { getProfile } from '../../../../services/axios/auth'

function TermsOfUse({ openState = [], setSnack }) {
  const dispatch = useAppDispatch()
  const navigate = useNavigate()
  const [open, setOpen] = openState
  const classes = useStyles()

  const { run } = useAsync({
    data: null,
    error: null,
  })

  const { run: runGetTerms, data: termsData } = useAsync({
    data: null,
    error: null,
  })

  const {
    control,
    handleSubmit,
    setError,
    watch,
    clearErrors,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(termsOfUseSchema),
    mode: 'onChange',
  })
  const isAccept = watch('isAccept')
  const handleClose = () => {
    setOpen(false)
    clearErrors()
  }

  const handleCloseDecline = () => {
    setOpen(false)
    clearErrors()
    navigate(-1)
  }

  const getTerms = async () => {
    try {
      const response = await api.get(API_GET_TERM_OF_USE)
      return Promise.resolve(response.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  const postTerms = async () => {
    try {
      const response = await api.post(API_POST_TERM_OF_USE, { terms_of_use: termsData.id })
      if (response.status === 201) {
        setSnack({
          open: true,
          header: ' !',
          message: '',
        })
        //   
        await dispatch(getProfile())
        handleClose()
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  const onSubmit = () => {
    clearErrors()
    run(postTerms())
  }

  //  
  useEffect(() => {
    runGetTerms(getTerms())
  }, [])

  return (
    <div>
      <Dialog maxWidth="xl" open={open} onClose={handleCloseDecline} disableScrollLock className={classes.root}>
        <IconButton onClick={handleCloseDecline} className={classes.closePopup}>
          <IconClose />
        </IconButton>
        <DialogTitle className={classes.dialogTitle}> </DialogTitle>
        <DialogContent className={classes.dialogContent}>
          <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="termsForm">
            <div className={classes.formContent}>
              <Typography className={classes.formText}>{termsData?.text}</Typography>
              <div className={classes.checkboxContainer}>
                <Controller
                  name="isAccept"
                  control={control}
                  className={classes.checkbox}
                  render={({ field }) => <CheckboxItem {...field} checked={isAccept} margin={{ marginBottom: 0 }} />}
                />
                <Typography className={classes.checkboxText}>
                               {' '}
                  <a
                    href={termsData?.confidential_url || 'https://practicum.yandex.ru'}
                    target="_blank"
                    rel="noreferrer noopener"
                  >
                     
                  </a>{' '}
                          .
                </Typography>
              </div>
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.dialogActions}>
          <FormHelperText error style={{ textAlign: 'center', margin: '5px 0 5px 0' }}>
            {errors.non_field_errors?.message || errors.isAccept?.message || errors.terms_of_use?.message || ' '}
          </FormHelperText>
          <Button form="termsForm" className={classes.submitButton} type="submit">
            
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

export default React.memo(TermsOfUse)
