import React, { useEffect } from 'react'
import { useInView } from 'react-intersection-observer'
import { Box, Typography } from '@mui/material'
import theme from '../../../../utils/themeMUI5'
import { VacancyCard } from '../index'
import { useAppSelector } from '../../../../services/store/hooks'
import LoadingOverlay from '../../../Sub/DataTable/LoadingOverlay/LoadingOverlay'

const PartnersVacanciesList = ({ pageState, haveMarketVacancy }: any) => {
  const [, setVacancyPage] = pageState
  const { vacancies, vacancyPage, isVacancyLoading } = useAppSelector((store) => store.studentVacancies)
  const { ref, inView, entry } = useInView({
    threshold: 0.5,
  })
  useEffect(() => {
    if (inView && vacancyPage) {
      setVacancyPage(vacancyPage)
    }
  }, [inView])

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        width: '1088px',
        marginBottom: '15px',
      }}
    >
      <Typography variant="h3">  </Typography>
      <Typography mt="4px" mb="36px" variant="body3" color={theme?.color?.black500}>
             ,      
      </Typography>

      <Box
        sx={
          haveMarketVacancy
            ? {
                display: 'flex',
                gap: '12px',
                height: '196px',
                overflowX: 'scroll',
                overflowY: 'hidden',
                scrollbarWidth: 'thin',
                scrollbarColor: '#1a1b2233 #ffffff',
                '&::-webkit-scrollbar': {
                  height: 5,
                },
                '&::-webkit-scrollbar-thumb': {
                  backgroundColor: '#1a1b2233',
                },
              }
            : {
                display: 'grid',
                flexWrap: 'wrap',
                gap: '12px',
                gridTemplateColumns: '1fr 1fr 1fr 1fr',
                gridTemplateRows: '1fr 1fr 1fr',
                width: '1070px',
                height: '544px',
                overflowX: 'hidden',
                overflowY: 'scroll',
                scrollbarWidth: 'thin',
                scrollbarColor: '#1a1b2233 #ffffff',
                '&::-webkit-scrollbar': {
                  height: 5,
                },
                '&::-webkit-scrollbar-thumb': {
                  backgroundColor: '#1a1b2233',
                },
              }
        }
      >
        {isVacancyLoading && <LoadingOverlay style={{ height: '40%', zIndex: 99 }} />}
        {vacancies &&
          vacancies?.map((vacancy, index) => {
            if (vacancies.length === index + 1) {
              return <VacancyCard isPartner vacancy={vacancy} ref={ref} />
            }
            return <VacancyCard isPartner vacancy={vacancy} />
          })}
      </Box>
    </Box>
  )
}

export default PartnersVacanciesList
