import React, { useEffect } from 'react'
import { Dialog, DialogContent, IconButton } from '@mui/material'

import { IconClose } from '../../../../assets/Icons'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import VacancyAbout from '../VacancyAbout/VacancyAbout'
import { closePartnersVacancyPopup } from '../../../../services/slices/student-vacancies-slice'
import { getPartnersVacancy } from '../../../../services/axios/student-vacancies'

const AboutPartnersVacancyPopup = () => {
  const dispatch = useAppDispatch()
  const {
    isOpenPartnersVacancyPopup,
    selectedVacancy: littleVacancy,
    selectedFullVacancy: selectedVacancy,
  } = useAppSelector((state) => state.studentVacancies)
  const handleClose = () => {
    dispatch(closePartnersVacancyPopup())
  }

  useEffect(() => {
    if (littleVacancy && isOpenPartnersVacancyPopup) dispatch(getPartnersVacancy(littleVacancy?.id))
  }, [isOpenPartnersVacancyPopup, littleVacancy])

  return (
    <Dialog
      open={isOpenPartnersVacancyPopup}
      onClose={handleClose}
      sx={{
        '& .MuiDialog-paper': {
          width: 710,
          minWidth: 710,
          maxWidth: 710,
          overflow: 'hidden',
          padding: 0,
        },
        '& .MuiDialog-container': {
          alignItems: 'flex-start',
        },
      }}
    >
      <IconButton
        onClick={handleClose}
        sx={{
          position: 'absolute',
          right: 18,
          top: 17,
          zIndex: 4,
        }}
      >
        <IconClose />
      </IconButton>
      <DialogContent sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
        {selectedVacancy && <VacancyAbout isPartner />}
      </DialogContent>
    </Dialog>
  )
}

export default React.memo(AboutPartnersVacancyPopup)
