import React, { useCallback, useState } from 'react'
import { Box } from '@mui/material'
import useStyles from './VacancyAboutList.style'
import Tabs from '../../../Sub/FormElements/custom-tabs/custom-tabs'
import Tab from '../../../Sub/FormElements/custom-tab/custom-tab'
import TabPanel from '../../../Sub/FormElements/tab-panel/tab-panel'
import VacancyAboutFirstTab from './VacancyAboutFirstTab'
import VacancyAboutSecondTab from './VacancyAboutSecondTab'

const VacancyAboutList = ({ isPartner }) => {
  const classes = useStyles()
  const [tabValue, setTabValue] = useState(0)

  const handleTabChange = useCallback((event, newValue) => {
    setTabValue(newValue)
  }, [])

  return (
    <Box
      sx={{
        padding: '38px 40px 60px 36px',
        maxHeight: 909,
        minHeight: 909,
        overflow: 'scroll',
        scrollbarWidth: 'thin',
        scrollbarColor: 'rgba(26, 27, 34, 0.2) #ffffff',
        '&::-webkit-scrollbar': {
          width: 4,
          // marginRight: 3,
        },
        '&::-webkit-scrollbar-thumb': {
          backgroundColor: 'rgba(26, 27, 34, 0.2)',
        },
      }}
      className={classes.vacancyAboutContainer}
    >
      <Tabs value={tabValue} onChange={handleTabChange}>
        <Tab sx={{ fontSize: '18px' }} label="" />
        <Tab sx={{ fontSize: '18px' }} label=" " />
      </Tabs>
      <TabPanel value={tabValue} index={0}>
        <VacancyAboutFirstTab isPartner={isPartner} />
      </TabPanel>
      <TabPanel value={tabValue} index={1}>
        <VacancyAboutSecondTab isPartner={isPartner} />
      </TabPanel>
    </Box>
  )
}

export default VacancyAboutList
