import React, { useEffect } from 'react'
import { useInView } from 'react-intersection-observer'
import { Box, Typography } from '@mui/material'
import theme from '../../../../utils/themeMUI5'
import { VacancyCard } from '../index'
import { useAppSelector } from '../../../../services/store/hooks'
import LoadingOverlay from '../../../Sub/DataTable/LoadingOverlay/LoadingOverlay'

const VacanciesList = ({ pageState }: any) => {
  const [, setExternalPage] = pageState
  const {
    externalVacancies: vacancies,
    isNoDetails,
    externalPage,
    isExternalLoading,
  } = useAppSelector((store) => store.studentVacancies)
  const { ref, inView } = useInView({
    threshold: 0.5,
  })
  useEffect(() => {
    if (inView && externalPage) {
      setExternalPage(externalPage)
    }
  }, [inView])

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        width: '1088px',
      }}
    >
      {!isNoDetails && <Typography variant="h3"> </Typography>}
      {!isNoDetails && (
        <Typography mt="4px" mb="36px" variant="body3" color={theme?.color?.black500}>
              ,       
        </Typography>
      )}

      <Box
        sx={{
          display: 'grid',
          flexWrap: 'wrap',
          gap: '12px',
          gridTemplateColumns: '1fr 1fr 1fr 1fr',
          gridTemplateRows: '1fr 1fr 1fr',
          width: '1070px',
          height: '544px',
          overflowX: 'hidden',
          overflowY: 'scroll',
          scrollbarWidth: 'thin',
          scrollbarColor: '#1a1b2233 #ffffff',
          '&::-webkit-scrollbar': {
            height: 5,
          },
          '&::-webkit-scrollbar-thumb': {
            backgroundColor: '#1a1b2233',
          },
        }}
      >
        {isExternalLoading && <LoadingOverlay style={{ height: '40%', zIndex: 99 }} />}

        {vacancies &&
          vacancies?.map((vacancy, index) => {
            if (vacancies.length === index + 1) {
              return <VacancyCard vacancy={vacancy} ref={ref} />
            }
            return <VacancyCard vacancy={vacancy} />
          })}
      </Box>
    </Box>
  )
}

export default VacanciesList
