/* eslint-disable no-unused-vars */
import React, { useMemo, useEffect, useState } from 'react'
import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { InputAdornment } from '@material-ui/core'
import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormHelperText,
  IconButton,
  Typography,
} from '@mui/material'
import useStyles from './ApplyForAPartnersJobPopup.styles'

import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import { ControlledSelectField, ControlledTextField } from '../../../Sub/FormElements'
import telegramIcon from '../../../../assets/telegram-icon.svg'
import emailIcon from '../../../../assets/email-icon.svg'
import Dropzone from '../../../Sub/Dropzone/Dropzone'
import { applyJobSchema, emailOption, telegramOption, urlOptionVacancyTab } from '../../../../utils/schemas'
import { PORTFOLIO_LETTER_SUPPORTED_FORMATS } from '../../../../utils/constants'
import useAsync from '../../../../hooks/useAsync'
import { formatApiDate } from '../../../../utils/dates'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { enqueueSnackbar } from '../../../../services/slices/app-slice'
import IconLeftArrow from '../../../../assets/Icons/IconLeftArrow/IconLeftArrow'
import { closeJobApplicationModal, openPartnersVacancyPopup } from '../../../../services/slices/student-vacancies-slice'
import { postPartnersVacancyResponse } from '../../../../services/axios/student-vacancies'
import processSliceServerError from '../../../../utils/server-slice-error'

const comMethods = [
  {
    id: 1,
    name: 'Telegram',
    icon: telegramIcon,
  },
  {
    id: 2,
    name: 'Email',
    icon: emailIcon,
  },
]

function ApplyForAPartnersJobPopup() {
  const dispatch = useAppDispatch()
  const { profile } = useAppSelector((store) => store.auth)
  const { isJobApplicationModalOpen, selectedFullVacancy: selectedVacancy } = useAppSelector(
    (store) => store.studentVacancies,
  )
  const [resumeOptions, setResumeOptions] = useState([])
  const [validationSchema, setValidationSchema] = useState(applyJobSchema)
  const [letterFile, setLetterFile] = useState([])
  const [extraResumeFile, setExtraResumeFile] = useState([])

  const { run } = useAsync({
    data: null,
    error: null,
  })

  const classes = useStyles()
  const {
    control,
    handleSubmit,
    setValue,
    register,
    watch,
    reset,
    setError,
    formState: { errors },
  } = useForm({
    defaultValues: { contact_type: profile?.contact_type, contact_info: profile?.contact_info },
    resolver: useMemo(() => yupResolver(validationSchema), [validationSchema]),
    mode: 'onChange',
  })

  const methodValue = watch('contact_type')
  const currentResume = watch('resume')
  const needToUploadExtraResume = currentResume === -1
  const isTelegram = methodValue === 1
  const contactPlaceholder = isTelegram ? '  ' : '  email'
  const contactClassName = isTelegram
    ? `${classes.resumeInput} ${classes.inputTelegram}`
    : `${classes.resumeInput} ${classes.inputEmail}`
  const contactAdornment = isTelegram
    ? {
        startAdornment: <InputAdornment position="start">@</InputAdornment>,
      }
    : {}

  const handleMethodChange = (e) => {
    setValue('contact_type', e.target.value)
    setValue('contact_info', '')
  }

  const handleClose = () => {
    dispatch(closeJobApplicationModal())
    reset()
    setLetterFile([])
    setExtraResumeFile([])
  }

  useEffect(() => {
    register('cover_letter')
    register('new_resume')
  }, [])

  useEffect(() => {
    setValue('cover_letter', letterFile)
  }, [letterFile])

  useEffect(() => {
    setValue('new_resume', extraResumeFile)
  }, [extraResumeFile])

  useEffect(() => {
    if (methodValue) {
      const scheme = applyJobSchema.omit(['contact_info'])
      const option = methodValue === 1 ? telegramOption : emailOption('  ')
      setValidationSchema(scheme.shape({ contact_info: option }))
    }
  }, [methodValue])

  useEffect(() => {
    if (selectedVacancy?.test_task_url) {
      setValidationSchema(applyJobSchema.shape({ completed_task_url: urlOptionVacancyTab }))
    } else setValidationSchema(applyJobSchema)
  }, [selectedVacancy?.test_task_url])

  useEffect(() => {
    if (isJobApplicationModalOpen) {
      const userResumes = profile?.resume
      let resumeItems = []
      setValue('contact_type', profile?.contact_type)
      setValue('contact_info', profile?.contact_info)
      if (userResumes && userResumes.length > 0) {
        resumeItems = [...userResumes]
      }
      setResumeOptions([...resumeItems, { id: -1, name: '    ' }])
    }
  }, [isJobApplicationModalOpen])

  const submitPostData = async (data) => {
    try {
      const response = await dispatch(postPartnersVacancyResponse(data)).unwrap()
      if (response.status?.ok) {
        dispatch(
          enqueueSnackbar({
            info: {
              type: 'common',
              title: ' ',
              message: '   ',
            },
            options: {
              key: new Date().getTime() + Math.random(),
              autoHideDuration: 6000,
            },
          }),
        )
      }
      handleClose()
      return Promise.resolve(response.data)
    } catch (e) {
      return processSliceServerError(e, setError)
    }
  }

  const onSubmit = async (data) => {
    const requestData = new FormData()
    requestData.append('partner_vacancy', selectedVacancy?.id)
    requestData.append('last_update_datetime', formatApiDate(new Date()))
    letterFile.forEach((file) => {
      requestData.append(`cover_letter`, file, file.name)
    })
    if (+data?.resume !== -1) {
      requestData.append('resume', data.resume)
    } else {
      extraResumeFile.forEach((file) => {
        requestData.append(`new_resume`, file, file.name)
      })
    }
    if (selectedVacancy?.test_task_url) {
      requestData.append('completed_task_url', data.completed_task_url)
    }
    run(submitPostData(requestData))
  }

  const onBackClick = () => {
    handleClose()
    dispatch(openPartnersVacancyPopup())
  }

  return (
    <div>
      <Dialog
        open={isJobApplicationModalOpen}
        onClose={handleClose}
        sx={{
          '& .MuiDialog-paper': {
            minWidth: 800,
            maxWidth: 880,
            height: 636,
            minHeight: 636,
            position: 'relative',
            padding: '76px 40px 40px 40px',
          },
        }}
      >
        <IconButton
          onClick={handleClose}
          variant="close-popup"
          sx={{ right: window.screen.width <= 900 ? '-40px' : undefined }}
        >
          <IconClose />
        </IconButton>
        <DialogTitle>  </DialogTitle>
        <DialogContent
          sx={{
            '&.MuiDialogContent-root': {
              minWidth: window.screen.width <= 900 ? 816 : 796,
            },
          }}
        >
          <Box
            sx={{
              position: 'absolute',
              display: 'flex',
              alignItems: 'center',
              gap: '12px',
              top: '32px',
              left: '40px',
              cursor: 'pointer',
            }}
            onClick={onBackClick}
          >
            <IconLeftArrow />
            <Typography variant="body3">  </Typography>
          </Box>
          <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="form-apply-for-a-job">
            <div className={classes.fieldContainerResume}>
              <Typography variant="body2" sx={{ marginBottom: '4px' }}>
                  
              </Typography>
              <div className={classes.twoFieldInRowContainer}>
                <ControlledSelectField
                  value={methodValue}
                  error={!!errors?.contact_type}
                  helperText={errors?.contact_type?.message}
                  onChange={handleMethodChange}
                  items={comMethods}
                  maxWidth={148}
                  disabled
                />
                <ControlledTextField
                  fieldName="contact_info"
                  control={control}
                  error={!!errors.contact_info?.message}
                  helperText={errors.contact_info?.message}
                  textClass={contactClassName}
                  placeholder={contactPlaceholder}
                  inputProps={contactAdornment}
                  maxWidth={226}
                  disabled
                />
              </div>
            </div>
            <Controller
              name="resume"
              control={control}
              render={({ field: { name, value, onChange } }) => (
                <ControlledSelectField
                  labelTitle=""
                  tipContent=" "
                  name={name}
                  value={value}
                  error={!!errors.resume?.message}
                  helperText={errors.resume?.message}
                  onChange={onChange}
                  items={resumeOptions}
                  maxWidth={386}
                />
              )}
            />
            <div>
              <Dropzone
                filesState={[letterFile, setLetterFile]}
                isLetter
                maxFilesNumber={1}
                label=" "
                nameTip="   : __"
                fileTip=" : .pdf, .doc, .docx, .txt,   10&nbsp;.&nbsp;"
                error={errors.cover_letter?.message}
                acceptedFormats={PORTFOLIO_LETTER_SUPPORTED_FORMATS}
              />
              <FormHelperText className={classes.formHelper}>{errors.cover_letter?.message || ' '}</FormHelperText>
            </div>
            {needToUploadExtraResume ? (
              <div>
                <Dropzone
                  filesState={[extraResumeFile, setExtraResumeFile]}
                  isLetter
                  maxFilesNumber={1}
                  label="   "
                  error={errors.new_resume?.message}
                />
                <FormHelperText className={classes.formHelper}>{errors.new_resume?.message || ' '}</FormHelperText>
              </div>
            ) : (
              <div />
            )}
            <div className={classes.textLink}>
              <ControlledTextField
                labelTitle="    "
                fieldName="completed_task_url"
                control={control}
                error={!!errors.completed_task_url?.message}
                helperText={errors.completed_task_url?.message || '   '}
                placeholder=" "
                disabled={!selectedVacancy?.test_task_url}
              />
            </div>
          </form>
        </DialogContent>
        <DialogActions>
          <FormHelperText error style={{ textAlign: 'center' }}>
            {errors.non_field_errors?.message || errors.partner_vacancy?.message || ''}
          </FormHelperText>
          <Button form="form-apply-for-a-job" className={classes.submitButton} type="submit">
            
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

export default React.memo(ApplyForAPartnersJobPopup)
