import React from 'react'
import { styled } from '@mui/material/styles'
import { SnackbarContent, SnackbarContentProps } from 'notistack'
import { LinearProgress, linearProgressClasses, ThemeProvider } from '@mui/material'
import theme from '../../../../utils/themeMUI5'
import useStyles from './NotificationSnackbar.styles'
import IconOk from '../../../../assets/Icons/IconOk/IconOk'
import IconSnackError from '../../../../assets/Icons/IconSnackError/IconSnackError'

const SnackLinearProgress = styled(LinearProgress)(() => ({
  height: 10,
  borderRadius: '0 0 12px 12px',
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor: 'transparent',
  },
  [`& .${linearProgressClasses.bar}`]: {
    transition: 'transform 6s linear',
    backgroundColor: '#5A9BFF',
  },
}))

interface NotificationSnackbarProps extends SnackbarContentProps {
  header: string
  message: string
}

const NotificationSnackbar = React.forwardRef<HTMLDivElement, NotificationSnackbarProps>((props, ref) => {
  const { header, message, ...other } = props
  const [progress, setProgress] = React.useState(100)
  const classes = useStyles()
  const isError = header === ' ' || header === '  '
  React.useEffect(() => {
    setProgress(0)
  }, [])

  return (
    <ThemeProvider theme={theme}>
      <SnackbarContent className={isError ? `${classes.root} ${classes.rootWhite}` : classes.root} ref={ref} {...other}>
        <div className={classes.content}>
          {isError ? <IconSnackError className={classes.icon} /> : <IconOk className={classes.icon} />}
          <div className={classes.messageContainer}>
            <span className={isError ? `${classes.header} ${classes.colorBlack}` : classes.header}>{header}</span>
            <span className={isError ? `${classes.message} ${classes.colorBlack}` : classes.message}>{message}</span>
          </div>
        </div>
        <LinearProgress
          variant="determinate"
          classes={{
            bar: classes.bar,
            root: classes.barRoot,
          }}
          value={progress}
        />
      </SnackbarContent>
    </ThemeProvider>
  )
})

export default NotificationSnackbar
