import { styled } from '@mui/material/styles'
import MuiAccordion, { AccordionProps } from '@mui/material/Accordion'
import MuiAccordionSummary, { AccordionSummaryProps } from '@mui/material/AccordionSummary'
import MuiAccordionDetails from '@mui/material/AccordionDetails'
import { Popover, ThemeProvider, Typography } from '@mui/material'
import React, { useEffect } from 'react'
import { Controller, useForm } from 'react-hook-form'
import { Box } from '@mui/system'
import { IconDeleteButton, IconDropdown } from '../../../../assets/Icons'
import theme from '../../../../utils/themeMUI5'
import CheckboxItem from '../../../Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import { ControlledMultiCityField, ControlledTextField } from '../../../Sub/FormElements'
import { TCity } from '../../../../services/types/global-types'

const Accordion = styled((props: AccordionProps) => <MuiAccordion disableGutters elevation={0} square {...props} />)(
  () => ({
    borderBottom: `1px solid #DDE0E4`,
    padding: '6px 0',
    '& .MuiAccordion-root': {},
    '&:last-child': {
      borderBottom: 0,
    },
    '&:before': {
      display: 'none',
    },
  }),
)

const AccordionSummary = styled(({ header, ...props }: AccordionSummaryProps & { header: string }) => (
  <MuiAccordionSummary expandIcon={<IconDropdown />} {...props}>
    <Typography sx={{ lineHeight: '20px', fontWeight: 500 }}>{header}</Typography>
  </MuiAccordionSummary>
))(() => ({
  padding: '0 5px',
  minHeight: '20px',
  '& .MuiAccordionSummary-content': {
    margin: '4px 0px 3px 3px',
  },
}))

const AccordionDetails = styled(MuiAccordionDetails)(() => ({
  padding: '11px 6px',
}))

const VacanciesPopoverFilter = ({ anchorsState, paramsState, visibilityState, checkboxesState, resetFilters }: any) => {
  const [anchorEl, setAnchorEl] = anchorsState
  const [expanded, setExpanded] = React.useState<string | false>('panel1')
  const {
    control,
    watch,
    reset,
    formState: { errors },
  } = useForm<any>({ defaultValues: { cities: [], salary: '', salary_max: '' } })
  const [requestParams, setRequestParams] = paramsState
  const values = watch()
  const [isPartnerVacancy, setIsPartnerVacancy] = visibilityState[0]
  const [isMarketVacancy, setIsMarketVacancy] = visibilityState[1]
  const [currencies, setCurrencies] = checkboxesState[0]
  const [employment, setEmployment] = checkboxesState[1]
  const [designFormats, setDesignFormats] = checkboxesState[2]
  const [status, setStatus] = checkboxesState[3]

  // useEffect(() => {
  //   setRequestParams({ ...requestParams, isMarketVacancy, isPartnerVacancy })
  // }, [isMarketVacancy, isPartnerVacancy])

  useEffect(() => {
    if (!status.new && !status.is_accepted && !status.is_showed) {
      setStatus({ ...status, new: true })
      return
    }
    setRequestParams({ ...requestParams, status })
  }, [status])

  useEffect(() => {
    setRequestParams({ ...requestParams, designFormats })
  }, [designFormats])

  useEffect(() => {
    setRequestParams({ ...requestParams, currency: currencies })
  }, [currencies])

  useEffect(() => {
    setRequestParams({ ...requestParams, employment })
  }, [employment])

  useEffect(() => {
    if (values?.cities?.length === 0 && (requestParams?.cities === 0 || !requestParams?.cities)) return
    const cities = values.cities?.map((city: TCity) => city?.id)
    if (cities && cities.length === 0) {
      const newParams = { ...requestParams }
      Object.keys(newParams).forEach((param) => {
        if (param === 'cities') delete newParams.cities
      })
      setRequestParams({ ...newParams })
      return
    }
    setRequestParams({ ...requestParams, cities })
  }, [values.cities])

  useEffect(() => {
    setRequestParams({ ...requestParams, salary: values.salary || undefined })
  }, [values.salary])

  useEffect(() => {
    setRequestParams({ ...requestParams, salary_max: values.salary_max || undefined })
  }, [values.salary_max])

  const handleChange = (panel: string) => (event: React.SyntheticEvent, newExpanded: boolean) => {
    setExpanded(newExpanded ? panel : false)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const resetFields = () => {
    reset({ cities: [], salary: '', salary_max: '' })
  }

  const open = Boolean(anchorEl)
  const id = open ? 'simple-popover' : undefined
  return (
    <ThemeProvider theme={theme}>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        sx={{
          '& .MuiPopover-paper': {
            width: '100%',
            maxWidth: 308,
            padding: '8px',
            borderRadius: '6px',
          },
        }}
      >
        <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
          <AccordionSummary aria-controls="panel1d-content" id="panel1d-header" header=" " />
          <AccordionDetails>
            <Controller
              name="isMarketVacancy"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel=" "
                  checked={isMarketVacancy}
                  margin={{ marginBottom: 8 }}
                  onChange={() => setIsMarketVacancy(!isMarketVacancy)}
                  // disabled
                />
              )}
            />
            <Controller
              name="isPartnerVacancy"
              control={control}
              render={({ field }) => (
                <CheckboxItem
                  {...field}
                  checkboxLabel=" "
                  checked={isPartnerVacancy}
                  margin={{ marginBottom: 0 }}
                  onChange={() => setIsPartnerVacancy(!isPartnerVacancy)}
                  // disabled
                />
              )}
            />
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel11'} onChange={handleChange('panel11')}>
          <AccordionSummary aria-controls="panel11d-content" id="panel11d-header" header=" " />
          <AccordionDetails>
            <Box sx={{ display: 'flex', width: '275px', flexDirection: 'column', gap: '3px' }}>
              <Controller
                name="new"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={status.new}
                    margin={{ marginBottom: 8 }}
                    onChange={() => setStatus({ ...status, new: !status.new })}
                  />
                )}
              />
              <Controller
                name="is_showed"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={status.is_showed}
                    margin={{ marginBottom: 8 }}
                    onChange={() => setStatus({ ...status, is_showed: !status.is_showed })}
                  />
                )}
              />
              <Controller
                name="is_accepted"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=" "
                    checked={status.is_accepted}
                    margin={{ marginBottom: 8 }}
                    onChange={() => setStatus({ ...status, is_accepted: !status.is_accepted })}
                  />
                )}
              />
            </Box>
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
          <AccordionSummary aria-controls="panel1d-content" id="panel1d-header" header="" />
          <AccordionDetails>
            <ControlledMultiCityField
              control={control}
              error={!!errors?.cities?.message}
              helperText={errors?.cities?.message}
              width={279}
              hideLabel
            />
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
          <AccordionSummary aria-controls="panel1d-content" id="panel1d-header" header="" />
          <AccordionDetails>
            <ControlledTextField
              fieldName="salary"
              control={control}
              error={!!errors.salary?.message}
              helperText={errors.salary?.message}
              placeholder=" "
              maxWidth={190}
              // disabled={isSalaryNotRequiredChecked}
            />
            <ControlledTextField
              fieldName="salary_max"
              control={control}
              error={!!errors.salary_max?.message}
              helperText={errors.salary_max?.message}
              placeholder=" "
              maxWidth={190}
              // disabled={isSalaryNotRequiredChecked}
            />
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
          <AccordionSummary aria-controls="panel1d-content" id="panel1d-header" header="" />
          <AccordionDetails>
            <Box sx={{ display: 'grid', width: '275px', gridTemplateColumns: '150px 1fr', rowGap: '10px' }}>
              <Controller
                name="rouble"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={currencies.ruble}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setCurrencies({ ...currencies, ruble: !currencies.ruble })}
                  />
                )}
              />
              <Controller
                name="dollar"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={currencies.dollar}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setCurrencies({ ...currencies, dollar: !currencies.dollar })}
                  />
                )}
              />
              <Controller
                name="euro"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={currencies.euro}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setCurrencies({ ...currencies, euro: !currencies.euro })}
                  />
                )}
              />
              <Controller
                name="pound"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={currencies.pound}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setCurrencies({ ...currencies, pound: !currencies.pound })}
                  />
                )}
              />
              <Controller
                name="shekel"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={currencies.shekel}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setCurrencies({ ...currencies, shekel: !currencies.shekel })}
                  />
                )}
              />
            </Box>
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel5'} onChange={handleChange('panel5')}>
          <AccordionSummary aria-controls="panel1d-content" id="panel1d-header" header=" " />
          <AccordionDetails>
            <Box sx={{ display: 'grid', width: '275px', gridTemplateColumns: '1fr 1fr 1fr', columnGap: '5px' }}>
              <Controller
                name="office"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={employment.office}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setEmployment({ ...employment, office: !employment.office })}
                  />
                )}
              />
              <Controller
                name="hybrid"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={employment.hybrid}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setEmployment({ ...employment, hybrid: !employment.hybrid })}
                  />
                )}
              />
              <Controller
                name="remote"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={employment.remote}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setEmployment({ ...employment, remote: !employment.remote })}
                  />
                )}
              />
            </Box>
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel6'} onChange={handleChange('panel6')}>
          <AccordionSummary aria-controls="panel1d-content" id="panel1d-header" header="" />
          <AccordionDetails>
            <Box sx={{ display: 'flex', width: '275px', flexDirection: 'column', gap: '5px' }}>
              <Controller
                name="dogovor"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=" "
                    checked={designFormats.dogovor}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setDesignFormats({ ...designFormats, dogovor: !designFormats.dogovor })}
                  />
                )}
              />
              <Controller
                name="samo"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={designFormats.samo}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setDesignFormats({ ...designFormats, samo: !designFormats.samo })}
                  />
                )}
              />
              <Controller
                name="ip"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={designFormats.ip}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setDesignFormats({ ...designFormats, ip: !designFormats.ip })}
                  />
                )}
              />
              <Controller
                name="contract"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={designFormats.contract}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setDesignFormats({ ...designFormats, contract: !designFormats.contract })}
                  />
                )}
              />
              <Controller
                name="gph"
                control={control}
                render={({ field }) => (
                  <CheckboxItem
                    {...field}
                    checkboxLabel=""
                    checked={designFormats.gph}
                    margin={{ marginBottom: 0 }}
                    onChange={() => setDesignFormats({ ...designFormats, gph: !designFormats.gph })}
                  />
                )}
              />
            </Box>
          </AccordionDetails>
        </Accordion>
        <Accordion expanded onChange={handleChange('panel7')}>
          <AccordionDetails>
            <Box
              onClick={() => resetFilters(resetFields)}
              sx={{ display: 'flex', alignItems: 'center', gap: '10px', cursor: 'pointer' }}
            >
              <IconDeleteButton />
              <Typography variant="body"> </Typography>
            </Box>
          </AccordionDetails>
        </Accordion>
      </Popover>
    </ThemeProvider>
  )
}

export default VacanciesPopoverFilter
