/* eslint-disable no-unused-vars */
/* eslint-disable camelcase */
import clsx from 'clsx'
import React from 'react'
import { Avatar, IconButton, Typography, Button, Dialog, Popover } from '@material-ui/core'
import { formatDayFullMonth, formatRusStyle } from '../../../utils/dates'
import IconDeleteButton from "../../../assets/Icons/IconDeleteButton/IconDeleteButton"
import IconClipInfo from "../../../assets/Icons/IconClipInfo/IconClipInfo"
import IconEllipse from "../../../assets/Icons/IconEllipse/IconEllipse"
import IconEditPencil from "../../../assets/Icons/IconEditPencil/IconEditPencil"

import useStyles from './FeedCard.style'

const Progress = ({ report, isUser }) => {
  const classes = useStyles(report)
  return (
    <div className={clsx(isUser && classes.bottomContainer, !isUser && classes.bottomContainerStudent)}>
      <div className={classes.progress}>
        <div className={classes.response} />
        <div className={classes.test_task} />
        <div className={classes.tech_interview} />
        <div className={classes.offer} />
      </div>
    </div>
  )
}

const FeedCard = React.forwardRef(({ report, onDeleteCard, isUser, onEdit }, ref) => {
  const { response, report_date, test_task, tech_interview, offer, comment, profile } = report
  const classes = useStyles()
  const [anchorEl, setAnchorEl] = React.useState(null)

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget)
  }

  const onEditCard = () => {
    onEdit()
  }
  const handleClose = () => {
    setAnchorEl(null)
  }
  const open = Boolean(anchorEl)
  const reportTotal = test_task + offer + response + tech_interview
  const [showDeletePopup, setDeletePopup] = React.useState(false)
  const closeDeletePopup = () => {
    setDeletePopup(false)
  }
  const openDeletePopup = () => {
    setDeletePopup(true)
  }
  const handleDeleteCard = () => {
    onDeleteCard(report.id)
    closeDeletePopup()
  }

  return (
    <div
      className={clsx(classes.root, !isUser && classes.rootStudentContainer, isUser && classes.rootUserContainer)}
      ref={ref}
    >
      <Dialog maxWidth="xl" open={showDeletePopup} onClose={closeDeletePopup} aria-labelledby="form-dialog-title">
        <div className={classes.rootDeletePopup}>
          <div className={classes.titleContainer}>
            <Typography variant="h2" id="form-dialog-title" className={classes.title}>
               
            </Typography>
            <Typography variant="body1" color="textSecondary">
                  ?
            </Typography>
          </div>
          <div className={classes.buttonContainer}>
            <Button className={classes.deleteButton} onClick={closeDeletePopup}>
              
            </Button>
            <Button className={classes.submitButton} onClick={handleDeleteCard}>
              
            </Button>
          </div>
        </div>
      </Dialog>
      {!isUser && <Avatar src={profile?.image} className={classes.avatar} />}
      <div className={isUser ? classes.rootUser : classes.rootStudent}>
        <div className={classes.topTextContainer}>
          <div className={classes.topContainer}>
            {profile && (
              <div className={isUser ? classes.userContainer : classes.studentContainer}>
                {isUser && <Avatar src={profile.image} className={classes.avatar} />}
                <div className={classes.textContainer}>
                  <div className={isUser ? classes.nameUserContainer : classes.nameStudentContainer}>
                    <Typography variant="body1">{profile.first_name}</Typography>
                    {isUser ? (
                      <Typography className={classes.subtitle} variant="body1">
                        {report_date ? formatDayFullMonth(report_date) : ''}
                      </Typography>
                    ) : (
                      <Typography variant="body1" className={classes.profile_cohort}>
                        {profile.course.name}, {profile.cohort_number} 
                      </Typography>
                    )}
                  </div>

                  {!isUser && (
                    <Typography className={classes.subtitle} variant="body1">
                      {report_date ? formatRusStyle(report_date) : ''}
                    </Typography>
                  )}
                </div>
              </div>
            )}
            {isUser && (
              <div className={classes.buttonContainer}>
                <IconButton onClick={onEditCard} className={classes.button}>
                  <IconEditPencil />
                </IconButton>
                <IconButton onClick={openDeletePopup} className={classes.button}>
                  <IconDeleteButton />
                </IconButton>
              </div>
            )}
          </div>

          <Typography className={isUser ? classes.userSubtitle : classes.subtitle} variant="body2">
            {comment}
          </Typography>
        </div>
        <div
          className={
            isUser
              ? `${classes.reportStatsContainer} ${classes.reportUserStatsContainer}`
              : classes.reportStatsContainer
          }
        >
          <Progress report={{ ...report, reportTotal }} isUser={isUser} />
          <Typography variant="body2">{`${response} / ${test_task} / ${tech_interview} / ${offer}`}</Typography>
          <IconButton onClick={handleClick} className={classes.button}>
            <IconClipInfo />
          </IconButton>
          <Popover
            open={open}
            anchorEl={anchorEl}
            onClose={handleClose}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            transformOrigin={{
              vertical: 'bottom',
              horizontal: 'left',
            }}
          >
            <ul className={classes.legend}>
              <li className={classes.legendItem}>
                {' '}
                <IconEllipse isGreen />
                <Typography>  </Typography>
              </li>
              <li className={classes.legendItem}>
                <IconEllipse isOrange />
                <Typography> </Typography>
              </li>
              <li className={classes.legendItem}>
                <IconEllipse isOrchid />
                <Typography> </Typography>
              </li>
              <li className={classes.legendItem}>
                <IconEllipse isBlue />
                <Typography></Typography>
              </li>
            </ul>
          </Popover>
        </div>
      </div>
    </div>
  )
})

export default FeedCard
