import React from 'react'
import { Box } from '@mui/system'
import useStyles from './VacancyTagsList.style'
import VacancyTag from '../VacancyTag/VacancyTag'
import { useAppSelector } from '../../../../services/store/hooks'

const VacancyTagsList = ({ isPartner }) => {
  const { selectedVacancy, directories } = useAppSelector((state) => state.studentVacancies)
  const classes = useStyles()

  return (
    (!!selectedVacancy?.cities?.length ||
      !!selectedVacancy?.test_task_url ||
      !!selectedVacancy?.design_formats?.length ||
      !!selectedVacancy?.work_format?.length ||
      !!selectedVacancy?.employment?.length) && (
      <Box className={classes.vacancyTags}>
        {isPartner && <VacancyTag title="  " />}
        {selectedVacancy?.cities &&
          selectedVacancy?.cities.map((city) => <VacancyTag key={city.location} title={city.location} />)}

        {selectedVacancy?.test_task_url && <VacancyTag title=" " />}
        {selectedVacancy?.design_formats &&
          selectedVacancy?.design_formats.map((item) => (
            <VacancyTag key={item} title={directories?.jobApplicationItems?.find((i) => i.id === item).value} />
          ))}
        {selectedVacancy?.work_format &&
          selectedVacancy?.work_format.map((item) => (
            <VacancyTag key={item} title={directories?.workFormatItems?.find((i) => i.id === item).value} />
          ))}
        {selectedVacancy?.employment &&
          selectedVacancy?.employment.map((item) => (
            <VacancyTag key={item} title={directories?.employmentItems?.find((i) => i.id === item).value} />
          ))}
      </Box>
    )
  )
}

export default VacancyTagsList
