import React from 'react'
import { styled } from '@mui/material/styles'
import { SnackbarContent, SnackbarContentProps } from 'notistack'
import { LinearProgress, linearProgressClasses, Box, Typography, ThemeProvider } from '@mui/material'
import theme from '../../../../utils/themeMUI5'
import useStyles from './AchievementSnackBar.styles'

const SnackLinearProgress = styled(LinearProgress)(() => ({
  height: 10,
  borderRadius: '0 0 12px 12px',
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor: 'transparent',
  },
  [`& .${linearProgressClasses.bar}`]: {
    transition: 'transform 6s linear',
    backgroundColor: '#5A9BFF',
  },
}))

interface AchievementSnackbarProps extends SnackbarContentProps {
  title: string
  image: string
  message: string
}

const AchievementSnackBar = React.forwardRef<HTMLDivElement, AchievementSnackbarProps>((props, ref) => {
  const { title, message, image, ...other } = props
  const [progress, setProgress] = React.useState(100)
  const classes = useStyles()

  React.useEffect(() => {
    setProgress(0)
  }, [])

  return (
    <ThemeProvider theme={theme}>
      <SnackbarContent className={classes.root} ref={ref} {...other}>
        <Box sx={{ display: 'flex', width: 350, minHeight: 84, padding: '20px 24px 0' }}>
          <img
            src={image}
            alt=""
            style={{ width: 64, maxHeight: 64, display: 'block', flexBasis: 64, flexShrink: 0 }}
          />
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center',
              overflow: 'hidden',
              marginLeft: '8px',
            }}
          >
            <Typography variant="body3" sx={{ color: '#1D6BF3', marginBottom: '4px' }}>
              {title}
            </Typography>
            <Typography variant="body2" style={{ fontWeight: 400, color: '#1D6BF3' }}>
              {message}
            </Typography>
          </Box>
        </Box>
        <SnackLinearProgress variant="determinate" value={progress} />
      </SnackbarContent>
    </ThemeProvider>
  )
})

export default AchievementSnackBar
