/* eslint-disable no-unused-vars */
import React, { FC, useEffect, useMemo } from 'react'
import debounce from 'lodash.debounce'
import { Typography, InputAdornment, Box } from '@mui/material'
import { useForm } from 'react-hook-form'
import useStyles from './VacanciesFilters.style'
import IconSearch from '../../../../assets/Icons/IconSearch/IconSearch'
import { ControlledTextField } from '../../../Sub/FormElements'
import { CustomDatePickerInterval } from '../../../Sub'
import IconFiltersSettings from '../../../../assets/Icons/IconFiltersSettings/IconFiltersSettings'
import theme from '../../../../utils/themeMUI5'
import StudentVacanciesPopoverFilter from '../VacanciesPopoverFilter/VacanciesPopoverFilter'
import { formatDbDate } from '../../../../utils/dates'
import { useAppDispatch } from '../../../../services/store/hooks'
import { resetPage } from '../../../../services/slices/student-vacancies-slice'

type Props = {
  // TODO   
  paramsState: any
  visibilityState: any
  checkboxesState: Array<any>
  resetFilters: (reset: () => void) => void
  dateState: Array<any>
}

const VacanciesFilters: FC<Props> = ({ paramsState, visibilityState, checkboxesState, resetFilters, dateState }) => {
  const dispatch = useAppDispatch()
  const classes = useStyles()
  const { control, watch } = useForm()
  const [requestParams, setRequestParams] = paramsState
  const [startDate, setStartDate] = dateState[0]
  const [endDate, setEndDate] = dateState[1]
  const [anchorEl, setAnchorEl] = React.useState<HTMLDivElement | null>(null)

  const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
    setAnchorEl(event.currentTarget)
  }

  const changeHandler = (value: any, { name }: any) => {
    if (name === 'search' && value.search.length < 3 && value.search.length > 0) return
    setRequestParams({ ...requestParams, ...value })
    dispatch(resetPage())
  }

  const debouncedChangeHandler = useMemo(() => debounce(changeHandler, 500), [])

  useEffect(() => () => debouncedChangeHandler.cancel(), [])

  useEffect(() => {
    const subscription = watch(debouncedChangeHandler)
    return () => subscription.unsubscribe()
  }, [watch])

  useEffect(() => {
    if (startDate) {
      setRequestParams({ ...requestParams, start_date: formatDbDate(startDate) })
      dispatch(resetPage())
    }
  }, [startDate])

  useEffect(() => {
    if (endDate) {
      setRequestParams({ ...requestParams, end_date: formatDbDate(endDate) })
      dispatch(resetPage())
    }
  }, [endDate])

  return (
    <>
      <StudentVacanciesPopoverFilter
        anchorsState={[anchorEl, setAnchorEl]}
        paramsState={paramsState}
        visibilityState={visibilityState}
        checkboxesState={checkboxesState}
        resetFilters={resetFilters}
      />
      <Box className={classes.root}>
        <Typography variant="h2" component="h1">
           
        </Typography>
        <div className={classes.firstRow}>
          <ControlledTextField
            fieldName="search"
            placeholder="     "
            control={control}
            width="575px"
            /* eslint-disable-next-line @typescript-eslint/ban-ts-comment */
            /* @ts-ignore */
            inputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <IconSearch />
                </InputAdornment>
              ),
            }}
            /* eslint-disable-next-line @typescript-eslint/ban-ts-comment */
            /* @ts-ignore */
          />
          <Box sx={{ maxHeight: '40px', width: '240px' }}>
            <CustomDatePickerInterval startDateState={[startDate, setStartDate]} endDateState={[endDate, setEndDate]} />
          </Box>
          <Box
            onClick={(e) => handleClick(e)}
            sx={{ display: 'flex', alignItems: 'center', gap: '8px', maxHeight: '40px', cursor: 'pointer' }}
          >
            <IconFiltersSettings />
            <Typography color={theme.color.black900} variant="body4">
               
            </Typography>
          </Box>
        </div>
      </Box>
    </>
  )
}

export default React.memo(VacanciesFilters)
