import React, { FC, useEffect, useState } from 'react'
import { ThemeProvider, Box } from '@mui/material'
import { useNavigate } from 'react-router-dom'
import theme from '../../../utils/themeMUI5'
import {
  VacanciesFilters,
  VacanciesList,
  PartnersVacanciesList,
  ApplyForAPartnersJobPopup,
  AboutPartnersVacancyPopup,
  AboutNonPartnersVacancyPopup,
  TermsOfUse,
} from '../../../components/Main/VacanciesPage'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import canUsePartnersVacancy from '../../../utils/canUsePartnersVacancy'
import {
  getExternalVacancies,
  getPartnersDirectories,
  getPartnersVacancies,
} from '../../../services/axios/student-vacancies'
import { TSetSnack } from '../../../services/types/global-types'
import { TStudentVacancyRequestParams } from '../../../services/types/student-vacancies/student-vacancies-types'
import { resetPage, resetPartnersPage } from '../../../services/slices/student-vacancies-slice'
import { PROJECT_START_DATE } from '../../../utils/constants'

type Props = {
  setSnack: TSetSnack
}

const VacanciesPage: FC<Props> = ({ setSnack }) => {
  const dispatch = useAppDispatch()
  const { profile } = useAppSelector((store) => store.auth)
  const { status, externalVacancies, vacancies } = useAppSelector((store) => store.studentVacancies)
  const [requestParams, setRequestParams] = useState<TStudentVacancyRequestParams>(null)
  const [openTermOfUse, setOpenTermOfUse] = useState(!profile?.terms_of_use_accepted)
  const [externalPage, setExternalPage] = useState(undefined)
  const [vacancyPage, setVacancyPage] = useState(undefined)
  const [isPartnerVacancy, setIsPartnerVacancy] = useState(true)
  const [isMarketVacancy, setIsMarketVacancy] = useState(true)
  const [startDate, setStartDate] = useState(PROJECT_START_DATE)
  const [endDate, setEndDate] = useState(Date.now())
  const [currencies, setCurrencies] = useState({
    dollar: true,
    euro: true,
    ruble: true,
    shekel: true,
    pound: true,
  })
  const [employment, setEmployment] = useState({
    office: true,
    hybrid: true,
    remote: true,
  })
  const [designFormats, setDesignFormats] = useState({
    dogovor: true,
    samo: true,
    ip: true,
    contract: true,
    gph: true,
  })
  const [vacancyStatus, setVacancyStatus] = useState({
    new: true,
    is_showed: true,
    is_accepted: true,
  })

  const resetFilters = (reset: () => void) => {
    setCurrencies({ dollar: true, euro: true, ruble: true, shekel: true, pound: true })
    setEmployment({ office: true, hybrid: true, remote: true })
    setDesignFormats({ dogovor: true, samo: true, ip: true, contract: true, gph: true })
    setVacancyStatus({ is_showed: true, is_accepted: true, new: true })
    if (!isMarketVacancy) setIsMarketVacancy(true)
    if (!isPartnerVacancy) setIsPartnerVacancy(true)
    setStartDate(PROJECT_START_DATE)
    setEndDate(Date.now())
    //     
    setExternalPage(undefined)
    setVacancyPage(undefined)
    dispatch(resetPage())
    reset()
    setRequestParams(null)
  }

  const navigate = useNavigate()

  const isLoading = status === 'pending'

  useEffect(() => {
    dispatch(getPartnersDirectories())
  }, [])

  useEffect(() => {
    if (!isMarketVacancy) {
      setVacancyPage(undefined)
      dispatch(resetPartnersPage())
      if (profile?.terms_of_use_accepted && !openTermOfUse && requestParams) {
        dispatch(getPartnersVacancies({ ...requestParams, limit: isMarketVacancy ? 5 : 16, page: vacancyPage }))
      }
    }
  }, [isMarketVacancy])

  useEffect(() => {
    setExternalPage(undefined)
    setVacancyPage(undefined)
    dispatch(resetPage())
  }, [requestParams])

  useEffect(() => {
    if (profile?.terms_of_use_accepted && !openTermOfUse && profile.terms_of_use_accepted && requestParams) {
      dispatch(getPartnersVacancies({ ...requestParams, limit: isMarketVacancy ? 5 : 16, page: vacancyPage }))
    }
  }, [requestParams, openTermOfUse, vacancyPage])

  useEffect(() => {
    if (profile?.terms_of_use_accepted && profile.terms_of_use_accepted && !openTermOfUse && requestParams) {
      dispatch(getExternalVacancies({ ...requestParams, limit: 16, page: externalPage }))
    }
  }, [requestParams, openTermOfUse, externalPage])

  const isAccessGranted = canUsePartnersVacancy(profile?.status)
  if (!isAccessGranted) navigate(-1)

  return (
    <ThemeProvider theme={theme}>
      <Box
        sx={{
          width: '100%',
          display: 'flex',
          flexDirection: 'column',
          boxSizing: 'border-box',
          minWidth: 1120,
          maxWidth: 1120,
          padding: '36px 0 96px 32px',
          position: 'relative',
        }}
      >
        <VacanciesFilters
          visibilityState={[
            [isPartnerVacancy, setIsPartnerVacancy],
            [isMarketVacancy, setIsMarketVacancy],
          ]}
          dateState={[
            [startDate, setStartDate],
            [endDate, setEndDate],
          ]}
          paramsState={[requestParams, setRequestParams]}
          checkboxesState={[
            [currencies, setCurrencies],
            [employment, setEmployment],
            [designFormats, setDesignFormats],
            [vacancyStatus, setVacancyStatus],
          ]}
          resetFilters={resetFilters}
        />
        <Box
          sx={{
            height: '1px',
            width: '1044px',
            background: '#DDE0E4',
            marginBottom: '64px',
          }}
        />
        {isPartnerVacancy && vacancies?.length > 0 && (
          <PartnersVacanciesList haveMarketVacancy={isMarketVacancy} pageState={[vacancyPage, setVacancyPage]} />
        )}
        {isMarketVacancy && externalVacancies?.length > 0 && (
          <VacanciesList pageState={[externalPage, setExternalPage]} />
        )}
        <Box mt="20px" />
        {/* <VacanciesList isNoDetails /> */}
      </Box>
      <TermsOfUse setSnack={setSnack} openState={[openTermOfUse, setOpenTermOfUse]} />
      <ApplyForAPartnersJobPopup />
      <AboutPartnersVacancyPopup />
      <AboutNonPartnersVacancyPopup />
    </ThemeProvider>
  )
}

export default VacanciesPage
