import React, { useState, Dispatch, SetStateAction } from 'react'
import { Toolbar, AppBar, IconButton, MenuItem, Menu } from '@material-ui/core'
import Button from '@mui/material/Button'
import { NavLink } from 'react-router-dom'
import useStyles from './Header.style'
import userIcon from '../../../assets/user-icon.svg'
import EditProfilePopup from '../GlobalPopups/EditProfilePopup/EditProfilePopup'
import IconLogo from '../../../assets/Icons/IconLogo/IconLogo'
import { isConsultant, isVacancyManager, isUser } from '../../../utils/check-auth'
import ExitPopup from '../GlobalPopups/ExitPopup/ExitPopup'
import IconBell from '../../../assets/Icons/IconBell/IconBell'
import IconLockPage from '../../../assets/Icons/IconLockPage/IconLockPage'
import canUsePartnersVacancy from '../../../utils/canUsePartnersVacancy'
// import { NavTabs, NavLinkTab } from '../form-elements'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { signOut } from '../../../services/axios/auth'
import { FoundJobPopup, VacationActionPopup } from '../GlobalPopups'

interface IHeaderProps {
  setSnack: Dispatch<SetStateAction<{ open: boolean; header: string; message: string }>>
}

const Header = ({ setSnack }: IHeaderProps) => {
  const dispatch = useAppDispatch()
  const { role, profile } = useAppSelector((store) => store.auth)
  const classes = useStyles()
  const [anchorEl, setAnchorEl] = useState<HTMLElement | null>(null)
  const [isProfilePopupOpen, setIsProfilePopupOpen] = useState(false)
  const [isJobModalOpen, setIsJobModalOpen] = useState(false)
  const [isVacationPopupOpened, setIsVacationPopupOpened] = React.useState(false)
  const open = Boolean(anchorEl)
  const [isOpenExitPopup, setIsOpenExitPopup] = useState(false)
  // const [tabIndex, setIndex] = React.useState(0)
  // const location = useLocation()
  // console.log(location?.pathname)

  // const handleChangeTab = (event, newValue) => {
  //   setIndex(newValue)
  // }

  const isPartnerLinkAccess = canUsePartnersVacancy(profile?.status)

  const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget)
  }

  const handleDisableClick = (event: React.MouseEvent<HTMLElement>) => {
    if (!isPartnerLinkAccess) event.preventDefault()
  }

  const handleClose = () => {
    setAnchorEl(null)
  }
  const handleEditProfile = () => {
    setAnchorEl(null)
    setIsProfilePopupOpen(true)
  }

  const handleOpenVacation = () => {
    setAnchorEl(null)
    setIsVacationPopupOpened(true)
  }

  const handleCloseExit = () => {
    dispatch(signOut())
    setAnchorEl(null)
  }

  const handleCloseExitPopup = () => {
    setIsOpenExitPopup(false)
  }

  const handleOpenExitPopup = () => {
    setIsOpenExitPopup(true)
  }
  const isConsultantRole = isConsultant(role)
  const isVacancyManagerRole = isVacancyManager(role)
  const isUserRole = isUser(role)

  // eslint-disable-next-line no-nested-ternary
  const linksBlock = isConsultantRole ? (
    // <NavTabs value={location?.pathname}>
    <>
      <NavLink
        className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`}
        to="/consultant/applications"
      >
        
      </NavLink>
      {/* <NavLinkTab label="" to="/consultant/applications" value="/consultant/applications" />
      <NavLinkTab label=" " to="/consultant/students" value="/consultant/students" />
      <NavLinkTab label="" to="/consultant/feed" value="/consultant/feed" />
      <NavLinkTab label="" to="/consultant/diary" value="/consultant/diary" /> */}

      <NavLink
        className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`}
        to="/consultant/students"
      >
         
      </NavLink>
      {/* TODO    */}
      {/* <NavLink */}
      {/*  className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} */}
      {/*  to="/consultant/statistics" */}
      {/* > */}
      {/*   */}
      {/* </NavLink> */}
      <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/consultant/feed">
        
      </NavLink>
      {/* <NavLink
        className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`}
        to="/consultant/diary"
      >
        
      </NavLink> */}
      <NavLink
        className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`}
        to="/consultant/cohorts"
      >
        
      </NavLink>
    </>
  ) : // </NavTabs>
  isVacancyManagerRole ? (
    <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/vacancies/list">
      
    </NavLink>
  ) : (
    <>
      <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/">
        
      </NavLink>
      <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/metrics">
        
      </NavLink>
      <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/feed">
        
      </NavLink>
      <NavLink
        onClick={handleDisableClick}
        className={({ isActive }) =>
          `${classes.link} ${classes.linkWithIcon} ${isActive && classes.link_active} ${
            !isPartnerLinkAccess && classes.disabledLink
          }`
        }
        to="/partners"
      >
         {!isPartnerLinkAccess && <IconLockPage />}
      </NavLink>
      <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/contacts">
        
      </NavLink>
    </>
  )

  return (
    <>
      {isUserRole && <EditProfilePopup openState={[isProfilePopupOpen, setIsProfilePopupOpen]} setSnack={setSnack} />}
      {isUserRole && (
        <FoundJobPopup
          openState={[isJobModalOpen, setIsJobModalOpen]}
          setSnack={setSnack}
          studentToEdit={undefined}
          vacancy={undefined}
          setPrevPopup={undefined}
          updateVacancies={undefined}
        />
      )}
      {isUserRole && (
        <VacationActionPopup openState={[isVacationPopupOpened, setIsVacationPopupOpened]} setSnack={setSnack} />
      )}
      <ExitPopup open={isOpenExitPopup} handleClose={handleCloseExitPopup} handleCloseExit={handleCloseExit} />
      <div className={classes.root}>
        <AppBar className={classes.appBar} position="static">
          <Toolbar className={classes.toolbar}>
            <div className={classes.linkContainer}>
              <div className={classes.logo}>
                <IconLogo type="black" />
              </div>

              {linksBlock}
            </div>
            <div className={classes.menu_container}>
              {isUserRole ? (
                <>
                  <Button
                    variant="outlined"
                    onClick={() => setIsJobModalOpen(true)}
                    sx={{
                      minHeight: 40,
                      maxHeight: 40,
                      minWidth: 152,
                      maxWidth: 152,

                      color: '#1D6BF3',
                      backgroundColor: '#fff',
                      textTransform: 'none',
                      fontFamily: 'YS Text',
                      fontSize: 14,
                      fontWeight: 500,
                      letterSpacing: 0,
                      lineHeight: '20px',
                      borderColor: '#fff',
                      marginRight: '56px',
                      borderRadius: '6px',
                      ':hover': {
                        color: '#fff',
                        borderColor: '#1D6BF3',
                        backgroundColor: '#1D6BF3',
                      },
                    }}
                  >
                     ?
                  </Button>
                  <IconButton
                    aria-label="account of current user"
                    aria-controls="menu-appbar"
                    aria-haspopup="true"
                    onClick={handleMenu}
                    color="inherit"
                  >
                    <img src={userIcon} alt="  " />
                  </IconButton>
                  <Menu
                    id="menu-appbar"
                    anchorEl={anchorEl}
                    anchorOrigin={{
                      vertical: 'top',
                      horizontal: 'right',
                    }}
                    keepMounted
                    transformOrigin={{
                      vertical: 'top',
                      horizontal: 'right',
                    }}
                    open={open}
                    onClose={handleClose}
                    classes={{
                      paper: classes.menu_border,
                    }}
                    className={classes.menu}
                  >
                    <MenuItem onClick={handleEditProfile}> </MenuItem>
                    <MenuItem onClick={handleOpenVacation}>  </MenuItem>
                    <MenuItem onClick={handleOpenExitPopup}></MenuItem>
                  </Menu>
                </>
              ) : (
                <div className={classes.buttonContainer}>
                  <IconButton>
                    <IconBell />
                  </IconButton>
                  <Button
                    variant="outlined"
                    onClick={handleOpenExitPopup}
                    sx={{
                      minHeight: 40,
                      maxHeight: 40,
                      minWidth: 83,
                      maxWidth: 83,

                      color: '#fff',
                      textTransform: 'none',
                      fontFamily: 'YS Text',
                      fontSize: 14,
                      borderColor: '#fff',
                      marginRight: '16px',
                      marginTop: '6px',
                      borderRadius: '6px',
                      ':hover': {
                        borderColor: '#fff',
                      },
                    }}
                  >
                    
                  </Button>
                </div>
              )}
            </div>
          </Toolbar>
        </AppBar>
      </div>
    </>
  )
}

export default Header
