import React from 'react'
import { Box, Typography } from '@mui/material'
import IconLogoCover from '../../../../assets/IconLogoCover.svg'
import theme from '../../../../utils/themeMUI5'
import { formatDayFullMonthYear } from '../../../../utils/dates'
import { IStudentVacancy } from '../../../../services/types/student-vacancies/student-vacancies-types'
import { useAppDispatch } from '../../../../services/store/hooks'
import {
  openPartnersVacancyPopup,
  openVacancyPopup,
  selectVacancy,
} from '../../../../services/slices/student-vacancies-slice'

type Props = {
  vacancy: IStudentVacancy
  isPartner?: boolean
}

const VacancyCard = React.forwardRef(({ vacancy, isPartner }: Props, ref) => {
  const dispatch = useAppDispatch()
  const { is_viewed: isViewed } = vacancy
  const isResponded = vacancy?.is_accepted

  const onCardClick = () => {
    dispatch(selectVacancy(vacancy))
    if (isPartner) {
      dispatch(openPartnersVacancyPopup())
    } else {
      dispatch(openVacancyPopup())
    }
  }
  return (
    <Box
      ref={ref}
      onClick={onCardClick}
      sx={{
        width: 250,
        minWidth: 250,
        height: 169,
        display: 'flex',
        position: 'relative',
        flexDirection: 'column',
        boxSizing: 'border-box',
        cursor: 'pointer',
        border: '1px solid #DDE0E4',
        borderRadius: '6px',
        backgroundColor: isViewed ? theme.color.black100 : undefined,
        padding: '16px',
        '&:hover': {
          backgroundColor: vacancy?.small_cover ? undefined : '#F1F6FF',
        },
        '&:before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundImage: vacancy?.small_cover ? `url(${vacancy?.small_cover})` : undefined,
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
          zIndex: 2,
          filter: isViewed ? 'grayscale(100%)' : undefined,
          borderRadius: '6px',
        },
        '&:after': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: vacancy?.small_cover
            ? 'linear-gradient(0deg, #1A1B22 0%, rgba(217, 217, 217, 0) 100%)'
            : undefined,
          zIndex: 3,
          borderRadius: '6px',
        },
      }}
    >
      {isViewed && (
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            gap: '4px',
            position: 'absolute',
            zIndex: 4,
            top: '76px',
            left: '16px',
          }}
        >
          <Typography
            variant="caption2"
            sx={{
              padding: '6px 12px',
              background: '#FFF',
              borderRadius: '4px',
            }}
          >
            
          </Typography>
          {isResponded && (
            <Typography
              variant="caption2"
              sx={{
                padding: '6px 12px',
                background: '#FFF',
                borderRadius: '4px',
              }}
            >
               
            </Typography>
          )}
        </Box>
      )}
      <Box sx={{ display: 'flex', alignItems: 'center', gap: '8px', zIndex: 4 }}>
        <Box
          sx={{
            width: '40px',
            height: '40px',
            borderRadius: '50%',
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'cover',
            backgroundImage: vacancy?.company_logo ? `url(${vacancy?.company_logo})` : `url(${IconLogoCover})`,
          }}
        />
        <Typography
          sx={{ zIndex: 4, maxWidth: 216, overflow: 'hidden', whiteSpace: 'nowrap', textOverflow: 'ellipsis' }}
          variant="body"
        >
          {vacancy?.company_name}
        </Typography>
      </Box>
      <Typography
        color={vacancy?.small_cover ? theme.color.white : theme.color.black900}
        sx={{ zIndex: 4, maxWidth: 216, overflow: 'hidden', whiteSpace: 'nowrap', textOverflow: 'ellipsis' }}
        variant="body3"
        mt="52px"
      >
        {vacancy?.position}
      </Typography>
      <Typography
        sx={{ zIndex: 4 }}
        mt="12px"
        variant="caption2"
        color={vacancy?.small_cover ? theme.color.white : theme.color.black500}
      >
        {vacancy?.start_date && formatDayFullMonthYear(vacancy?.start_date)}
      </Typography>
    </Box>
  )
})

export default VacancyCard
