import React from 'react'
import { Typography, Box } from '@mui/material'
import Button from '@mui/material/Button'
import { formatDayFullMonthYear } from '../../../../utils/dates'
import useStyles from './VacancyAboutHeader.style'
import theme from '../../../../utils/themeMUI5'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { enqueueSnackbar } from '../../../../services/slices/app-slice'
import {
  closePartnersVacancyPopup,
  closeVacancyPopup,
  openJobApplicationModal,
} from '../../../../services/slices/student-vacancies-slice'
import IconLogoCover from '../../../../assets/IconLogoCover.svg'
import { createVacancyResponse } from '../../../../services/axios/student-vacancies'

const VacancyAboutHeader = ({ isPartner }) => {
  const { directories, selectedFullVacancy: selectedVacancy } = useAppSelector((state) => state.studentVacancies)
  const classes = useStyles()
  const dispatch = useAppDispatch()

  const handleCreateResponse = async () => {
    const message = {
      open: true,
      title: ' ',
      message: '  ',
    }
    try {
      const result = await dispatch(createVacancyResponse(selectedVacancy?.id)).unwrap()
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'common',
            ...message,
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
      dispatch(closeVacancyPopup())
      return Promise.resolve(result)
    } catch (e) {
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'error',
            title: ' ',
            message: '   ',
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
      return 0
      // return Promise.reject(e)
    }
  }

  const openJobModal = () => {
    dispatch(closePartnersVacancyPopup())
    dispatch(openJobApplicationModal())
  }
  return (
    <Box
      sx={{
        width: '100%',
        boxSizing: 'border-box',
        padding: '24px 41px 29px 36px',
        display: 'flex',
        flexDirection: 'column',
        backgroundColor: !isPartner && 'hsla(219, 100%, 97%, 1)',
        minHeight: '236px',
        maxHeight: '236px',
        borderRadius: '6px',
        border: 'none',
        position: 'relative',
        '&:before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundImage: isPartner && selectedVacancy.big_cover ? `url(${selectedVacancy.big_cover})` : undefined,
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
          zIndex: 2,
          filter: isPartner && selectedVacancy?.is_viewed && 'grayscale(100%)',
        },
        '&:after': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: isPartner && 'linear-gradient(0deg, #1A1B22 0%, rgba(217, 217, 217, 0) 100%)',
          zIndex: 3,
        },
      }}
    >
      {selectedVacancy?.is_viewed && (
        <Typography
          variant="caption2"
          sx={{
            position: 'absolute',
            top: '24px',
            right: '60px',
            padding: '6px 12px',
            background: '#FFF',
            borderRadius: '4px',
            zIndex: 4,
          }}
        >
          
        </Typography>
      )}
      <Box
        sx={{
          display: 'flex',
          gap: '8px',
          position: 'relative',
          alignItems: 'center',
          zIndex: 4,
        }}
      >
        <Box
          component="img"
          src={selectedVacancy?.company_logo ? selectedVacancy?.company_logo : IconLogoCover}
          alt=" "
          sx={{
            width: '48px',
            height: '48px',
            objectFit: 'contain',
            position: 'relative',
            zIndex: 4,
            borderRadius: '50%',
          }}
        />
        <Typography
          variant="h3"
          sx={{
            position: 'relative',
            zIndex: 4,
            maxWidth: '470px',
            overflow: 'hidden',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
          }}
        >
          {selectedVacancy?.company_name}
        </Typography>
      </Box>
      <Typography
        mt="36px"
        variant="h2"
        color={isPartner ? theme.color.white : theme.color.black900}
        sx={{
          position: 'relative',
          zIndex: 4,
          maxWidth: '500px',
          overflowX: 'hidden',
          whiteSpace: 'nowrap',
          textOverflow: 'ellipsis',
          fontFamily: 'YS Display',
          minHeight: '32px',
        }}
      >
        {selectedVacancy?.position}
      </Typography>
      <Typography
        mt="4px"
        mb="16px"
        variant="body2"
        color={isPartner ? theme.color.white : theme.color.black500}
        sx={{ position: 'relative', zIndex: 4 }}
      >
        {selectedVacancy?.start_date && formatDayFullMonthYear(selectedVacancy?.start_date)}
      </Typography>
      <Box sx={{ width: '100%', display: 'flex', justifyContent: 'space-between' }}>
        <Box sx={{ display: 'flex', gap: '16px' }}>
          {isPartner && (
            <Button
              onClick={openJobModal}
              disabled={selectedVacancy?.is_accepted}
              sx={{
                position: 'relative',
                zIndex: 4,
                minWidth: '134px',
                '&:disabled': {
                  backgroundColor: theme.color.blueMain,
                  color: '#FFF',
                },
              }}
            >
              {selectedVacancy?.is_accepted ? ' ' : ''}
            </Button>
          )}
          {isPartner && selectedVacancy?.vacancy_url && (
            <Button
              variant="outlined"
              sx={{ position: 'relative', zIndex: 4, minWidth: '174px', outline: 'none !important' }}
              onClick={() => window.open(selectedVacancy?.vacancy_url, '_blank')}
            >
                
            </Button>
          )}
          {!isPartner && selectedVacancy?.vacancy_url && (
            <Button
              onClick={() => window.open(selectedVacancy?.vacancy_url, '_blank')}
              sx={{
                position: 'relative',
                zIndex: 4,
                minWidth: '174px',
                maxWidth: '174px',
                outline: 'none !important',
              }}
            >
                
            </Button>
          )}
          {!isPartner && (
            <Button
              disabled={selectedVacancy?.is_accepted}
              variant="outlined"
              onClick={handleCreateResponse}
              sx={{
                position: 'relative',
                zIndex: 4,
                minWidth: '160px',
                outline: 'none !important',
                width: !selectedVacancy?.is_accepted ? '221px' : undefined,
              }}
            >
              {selectedVacancy?.is_accepted ? ' ' : '   '}
            </Button>
          )}
        </Box>
        <Box sx={{ display: 'flex' }}>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            {selectedVacancy.salary !== null && (
              <Typography
                color={isPartner ? theme.color.white : theme.color.black900}
                variant="body3"
                sx={{ marginRight: '4px', position: 'relative', zIndex: 4 }}
              >
                {' '}
                <span className={isPartner ? `${classes.salarySpan} ${classes.salarySpanWhite}` : classes.salarySpan}>
                  {selectedVacancy?.salary}
                </span>
              </Typography>
            )}
            {selectedVacancy.salary_max !== null && (
              <Typography
                color={isPartner ? theme.color.white : theme.color.black900}
                variant="body3"
                sx={{ position: 'relative', zIndex: 4 }}
              >
                {' '}
                <span className={isPartner ? `${classes.salarySpan} ${classes.salarySpanWhite}` : classes.salarySpan}>
                  {selectedVacancy?.salary_max}
                </span>
              </Typography>
            )}
          </Box>
          {(selectedVacancy.salary_max !== null || selectedVacancy.salary !== null) && (
            <img
              alt=" "
              src={directories?.currencies?.find((item) => item?.id === selectedVacancy?.currency)?.icon}
              className={`${classes.currencyIcon} ${classes.currencyIconPopup}`}
            />
          )}
        </Box>
      </Box>
    </Box>
  )
}

export default VacancyAboutHeader
